body {
  background-color: #eaeaea;
  padding: 0px;
  margin: 0;
  color: black;
}

.about p {
  margin: 0;
}

.contact p {
  margin: 0;
}

h1, h2, p, ul, li {
  font-family: sans-serif;
}

h2 {
  font-size: 48px;

}

h3 {
  font-family: sans-serif;
  font-size: 20px;
}
ul.header li {
  display: inline;
  list-style-type: none;
  margin: 0;
}

.content {
  background-color: #e2e0e0;
  padding: 20px;
  padding-left: 15%;
  padding-right: 15%;
  word-wrap: break-word;
}
.content h2 {
  padding: 0;
  margin: 0;
}

/*
.active {
  background-color: #6D7993;
}
*/

.link:hover {
  background-color: #6D7993;
  cursor: default;
  -o-transition:.25s;
  -webkit-transition:.25s;
  transition:.25s;
}

.flag {
  height: 13.2px;
  width: 21.6px;
  border: 1px solid #96858F;
}

.menuButton {
  float: right;
  height: 45px;
  width: 45px;
}

.flag-li {
  padding: 20.5px;
}

.flag-li:hover {
  background-color: #6D7993;
  -o-transition:.25s;
  -webkit-transition:.25s;
  transition:.25s;
}

.mikko {
  width: 30%;
  height: 50%;
  border: .1px solid #96858F;
}

.aboutme_projects {
  float: left;
  padding-top: 5px;
}

.about_list {
  padding-bottom: 5px;
}

.small-menu-classname ul.header li {
    display: block;
}

ul.header {
  display:block;
  vertical-align: top;
  background-color: #D5D5D5;
  padding: 0;
  width: 100%;
}

ul.header li a {
  color: #6D7993;
  font-weight: bold;
  text-decoration: none;
  padding: 20px;
  display: inline-block;
}

.small-menu-classname ul.header li:hover {
    color: white;
    background-color: #6D7993;
    -o-transition:.25s;
    -webkit-transition:.25s;
    transition:.25s;
}

ul.header li a:hover {
  color: white;
}



